.contain{
  width: 100vw;
  height:100vh;
  display:flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  place-items:center;
  background:linear-gradient(to bottom,royalblue,rgb(46, 99, 255));

}

button{
  width:25vmin;
  height:5vmin;
  border:none;
  outline:none;
  margin:1vmin;

}


button:hover{
  cursor:pointer;
}
h1{
  color:white;
  font-size:15vmin;
}